import React, { useState } from "react";

// QuickStartGuide Component
const QuickStartGuide: React.FC<{ className?: string }> = ({ className }) => {
  const [currentStep, setCurrentStep] = useState(1);
  const [activeLanguage, setActiveLanguage] = useState("JavaScript");

  const baseUrl = "https://api.kidjis.com/provider";
  const languages = Object.keys(CODE_EXAMPLES);

  return (
    <div className={`relative w-full max-w-5xl mx-auto ${className}`}>
      <BackgroundShapes />

      <div className="relative z-10 p-8 rounded-2xl glass-container">
        <div className="flex justify-between items-center mb-8">
          <h2 className="text-3xl font-bold bg-gradient-to-r from-blue-400 to-purple-500 bg-clip-text text-transparent">
            Quick Start
          </h2>
          <a
            href="#"
            className="text-sm flex items-center gap-1 text-blue-400 hover:text-blue-300 transition-colors"
          >
            Full Documentation <ExternalLinkIcon size={14} />
          </a>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-12 gap-6 mb-8">
          <div className="md:col-span-5 space-y-4">
            <StepsIndicator
              steps={API_STEPS.map((step) => ({
                title: step.title,
                description: step.description,
              }))}
              currentStep={currentStep}
              onChange={setCurrentStep}
            />
          </div>

          <div className="md:col-span-7">
            {API_STEPS.map((step, index) => (
              <StepContent
                key={index}
                step={index + 1}
                title={step.title}
                description={step.description}
                image={step.image}
                isActive={currentStep === index + 1}
                isCompleted={currentStep > index + 1}
              />
            ))}
          </div>
        </div>

        <div className="mb-6">
          <div className="flex justify-between items-center mb-4">
            <h3 className="font-semibold text-xl">Base URL</h3>
          </div>
          <div className="bg-[#121726] p-3 rounded-lg text-gray-300 flex justify-between items-center">
            <code className="text-sm">{baseUrl}</code>
            <button
              onClick={() => navigator.clipboard.writeText(baseUrl)}
              className="p-1.5 rounded-md hover:bg-white/10 transition-colors"
              aria-label="Copy URL"
            >
              <CopyIcon className="h-4 w-4 text-gray-400" />
            </button>
          </div>
        </div>

        <div>
          <h3 className="font-semibold text-xl mb-4">
            Make your first API call
          </h3>

          <LanguageSelector
            languages={languages}
            activeLanguage={activeLanguage}
            onChange={setActiveLanguage}
          />

          {languages.map((language) => (
            <CodeBlock
              key={language}
              language={language}
              code={CODE_EXAMPLES[language as keyof typeof CODE_EXAMPLES]}
              isActive={activeLanguage === language}
            />
          ))}

          <div className="mt-8 p-4 bg-[#121726] rounded-lg">
            <h3 className="font-semibold mb-2">Authorization</h3>
            <p className="text-sm text-gray-300 mb-4">
              Include your API key in the X-Api-Key header for all requests:
            </p>
            <div className="bg-blue-900/50 text-white text-sm py-1.5 px-3 rounded inline-block">
              X-Api-Key: YOUR_API_KEY
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

// BackgroundShapes Component
const BackgroundShapes: React.FC = () => {
  return (
    <div className="absolute inset-0 overflow-hidden pointer-events-none">
      {/* Circle shape */}
      <div className="absolute top-10 left-[10%] w-48 h-48 rounded-full bg-blue-600/20 animate-float" />

      {/* Square shape */}
      <div className="absolute top-[30%] right-[10%] w-32 h-32 bg-purple-500/20 animate-rotate-slow" />

      {/* Triangle shape (created with CSS) */}
      <div
        className="absolute bottom-[20%] left-[15%] w-0 h-0 
                    border-l-[50px] border-l-transparent
                    border-b-[80px] border-b-teal-500/20
                    border-r-[50px] border-r-transparent
                    animate-pulse-slow"
      />

      {/* Ring shape */}
      <div
        className="absolute bottom-[30%] right-[20%] w-64 h-64 rounded-full border-8 border-blue-400/20 animate-float"
        style={{ animationDelay: "-3s" }}
      />

      {/* Small dots */}
      <div
        className="absolute top-[40%] left-[30%] w-6 h-6 rounded-full bg-yellow-400/30 animate-float"
        style={{ animationDelay: "-2s" }}
      />
      <div className="absolute top-[65%] right-[35%] w-4 h-4 rounded-full bg-pink-500/30 animate-pulse-slow" />
      <div
        className="absolute top-[20%] right-[45%] w-8 h-8 rounded-full bg-green-400/20 animate-float"
        style={{ animationDelay: "-4s" }}
      />

      {/* Large blob */}
      <div
        className="absolute -bottom-20 -left-20 w-80 h-80 rounded-full bg-indigo-500/10 animate-pulse-slow"
        style={{ animationDelay: "-1s" }}
      />
    </div>
  );
};

// StepContent Component
interface StepContentProps {
  step: number;
  title: string;
  description: string;
  image: string;
  isActive: boolean;
  isCompleted: boolean;
}

const StepContent: React.FC<StepContentProps> = ({
  step,
  title,
  description,
  image,
  isActive,
  isCompleted,
}) => {
  return (
    <div
      className={cn(
        "opacity-0 transition-all duration-500 ease-in-out",
        isActive ? "animate-fade-in opacity-100" : "hidden"
      )}
    >
      <div className="relative w-full h-full overflow-hidden rounded-lg glass-effect">
        <div className="absolute inset-0 flex items-center justify-center">
          <img src={image} alt={title} className="w-full h-full object-cover" />
        </div>
        <div className="absolute bottom-0 left-0 right-0 p-4 bg-black/40 backdrop-blur-sm">
          <h3 className="text-xl font-bold text-white">Step {step}</h3>
        </div>
      </div>
    </div>
  );
};

// StepsIndicator Component
interface StepItem {
  title: string;
  description: string;
}

interface StepsIndicatorProps {
  steps: StepItem[];
  currentStep: number;
  onChange: (step: number) => void;
}

const StepsIndicator: React.FC<StepsIndicatorProps> = ({
  steps,
  currentStep,
  onChange,
}) => {
  return (
    <div className="flex flex-col space-y-4">
      {steps.map((step, index) => {
        const stepNumber = index + 1;
        const isActive = stepNumber === currentStep;
        const isCompleted = stepNumber < currentStep;

        return (
          <button
            key={index}
            onClick={() => onChange(stepNumber)}
            className={cn(
              "flex items-start p-4 text-left rounded-lg transition-all duration-300",
              isActive
                ? "bg-blue-900/30 border border-blue-500/50"
                : "bg-transparent hover:bg-blue-900/20"
            )}
          >
            <div
              className={cn(
                "flex items-center justify-center w-10 h-10 rounded-full text-white font-bold mr-3",
                isActive
                  ? "bg-blue-600"
                  : isCompleted
                  ? "bg-blue-500"
                  : "bg-gray-700"
              )}
            >
              {stepNumber}
            </div>
            <div className="flex-1">
              <h3
                className={cn(
                  "text-lg font-semibold",
                  isActive ? "text-white" : "text-gray-300"
                )}
              >
                {step.title}
              </h3>
              {isActive && (
                <p className="text-gray-300 mt-2 text-sm animate-fade-in">
                  {step.description}
                </p>
              )}
            </div>
          </button>
        );
      })}
    </div>
  );
};

// LanguageSelector Component
interface LanguageSelectorProps {
  languages: string[];
  activeLanguage: string;
  onChange: (language: string) => void;
}

const LanguageSelector: React.FC<LanguageSelectorProps> = ({
  languages,
  activeLanguage,
  onChange,
}) => {
  return (
    <div className="flex space-x-2 mb-4">
      {languages.map((language) => (
        <button
          key={language}
          onClick={() => onChange(language)}
          className={cn(
            "px-3 py-1.5 rounded-md text-sm font-medium transition-colors",
            activeLanguage === language
              ? "bg-blue-600 text-white"
              : "bg-gray-800 text-gray-300 hover:bg-gray-700"
          )}
        >
          {language}
        </button>
      ))}
    </div>
  );
};

// CodeBlock Component
interface CodeBlockProps {
  language: string;
  code: string;
  isActive: boolean;
}

const CodeBlock: React.FC<CodeBlockProps> = ({ language, code, isActive }) => {
  const [copied, setCopied] = React.useState(false);

  const copyToClipboard = () => {
    navigator.clipboard.writeText(code);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <div
      className={cn(
        "relative rounded-lg bg-[#121726] p-4 text-sm opacity-0",
        isActive ? "animate-fade-in" : "hidden"
      )}
    >
      <div className="absolute top-3 right-3 flex">
        <button
          onClick={copyToClipboard}
          className="p-1.5 rounded-md transition-colors hover:bg-white/10"
          aria-label="Copy code"
        >
          {copied ? (
            <CheckIcon className="h-4 w-4 text-green-400" />
          ) : (
            <CopyIcon className="h-4 w-4 text-gray-400" />
          )}
        </button>
      </div>
      <pre className="overflow-x-auto text-gray-300 text-xs md:text-sm">
        <code>{code}</code>
      </pre>
    </div>
  );
};

// Define the structure of our API steps
const API_STEPS = [
  {
    title: "Create Account",
    description:
      "Sign up and create your account to get started with Kidjis API. You'll need to provide your basic information to set up your developer account.",
    image: "/lovable-uploads/1b9a6187-7010-488f-a7c1-5c596c6afe23.png",
  },
  {
    title: "Get API Key",
    description:
      "Generate your API key from the API Keys section in your dashboard. This key will be used to authenticate your requests.",
    image: "/lovable-uploads/1b9a6187-7010-488f-a7c1-5c596c6afe23.png",
  },
  {
    title: "Make API Calls",
    description:
      "Use your API key to authenticate and start making API calls to access our powerful services.",
    image: "/lovable-uploads/1b9a6187-7010-488f-a7c1-5c596c6afe23.png",
  },
];

// Code examples for different languages
const CODE_EXAMPLES = {
  JavaScript: `const axios = require('axios');

// Base URL for all API calls
const baseUrl = 'https://api.kidjis.com/provider';

// Example: Chat completion with GPT-3.5
const response = await axios.post(
  \`\${baseUrl}/api/v1/openai/chat/gpt-3.5-turbo\`,
  {
    prompt: "What is the capital of France?",
    stream: false,
    config: {
      temperature: 0.7,
      maxOutputTokens: 4096,
      topP: 1,
      topK: 40
    }
  },
  {
    headers: {
      'X-Api-Key': 'YOUR_API_KEY',
      'Content-Type': 'application/json'
    }
  }
);

console.log(response.data);`,

  Python: `import requests

# Base URL for all API calls
base_url = "https://api.kidjis.com/provider"

# Example: Chat completion with GPT-3.5
response = requests.post(
    f"{base_url}/api/v1/openai/chat/gpt-3.5-turbo",
    json={
        "prompt": "What is the capital of France?",
        "stream": False,
        "config": {
            "temperature": 0.7,
            "maxOutputTokens": 4096,
            "topP": 1,
            "topK": 40
        }
    },
    headers={
        "X-Api-Key": "YOUR_API_KEY",
        "Content-Type": "application/json"
    }
)

print(response.json())`,

  cURL: `curl -X POST "https://api.kidjis.com/provider/api/v1/openai/chat/gpt-3.5-turbo" \\
  -H "X-Api-Key: YOUR_API_KEY" \\
  -H "Content-Type: application/json" \\
  -d '{
    "prompt": "What is the capital of France?",
    "stream": false,
    "config": {
      "temperature": 0.7,
      "maxOutputTokens": 4096,
      "topP": 1,
      "topK": 40
    }
  }'`,
};

// Helper function for conditionally joining class names
const cn = (...classes: (string | boolean | undefined)[]) => {
  return classes.filter(Boolean).join(" ");
};

// Icon components
const ExternalLinkIcon: React.FC<{ size?: number; className?: string }> = ({
  size = 24,
  className,
}) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    width={size}
    height={size}
    viewBox="0 0 24 24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round"
    className={className}
  >
    <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6" />
    <polyline points="15 3 21 3 21 9" />
    <line x1="10" y1="14" x2="21" y2="3" />
  </svg>
);

const CopyIcon: React.FC<{ className?: string }> = ({ className }) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    width="24"
    height="24"
    viewBox="0 0 24 24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round"
    className={className}
  >
    <rect width="14" height="14" x="8" y="8" rx="2" ry="2" />
    <path d="M4 16c-1.1 0-2-.9-2-2V4c0-1.1.9-2 2-2h10c1.1 0 2 .9 2 2" />
  </svg>
);

const CheckIcon: React.FC<{ className?: string }> = ({ className }) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    width="24"
    height="24"
    viewBox="0 0 24 24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round"
    className={className}
  >
    <polyline points="20 6 9 17 4 12" />
  </svg>
);

export default QuickStartGuide;
